import React from 'react'
import PropTypes from 'prop-types'
import { Table } from 'antd'

const OuterTable = ({ tableData, columns }) => {
  let id = 1
  const newTable = []
  tableData.map(eachRow => {
    eachRow = { ...eachRow }
    eachRow.key = ++id
    const children = []
    let newId = 1
    const newObj = {}

    Object.keys(eachRow.PriceCategoryResult).map(eachDate => {
      const dateVal = eachDate.toString()
      newObj.key = ++newId
      newObj[dateVal] = [
        <p key={newId + 6}> {eachRow.PriceCategoryResult[eachDate].Cost}</p>,
        <p key={newId + 5}> {eachRow.PriceCategoryResult[eachDate].Energy}</p>,
        <p key={newId + 4}> {eachRow.PriceCategoryResult[eachDate].Power}</p>,
        <p key={newId + 2}> {eachRow.PriceCategoryResult[eachDate].Transfer}</p>,
        <p key={newId + 3}> {eachRow.PriceCategoryResult[eachDate].Infrastructure}</p>,
      ]
      eachRow[dateVal] = eachRow.PriceCategoryResult[eachDate].Delta
    })
    children.push(newObj)
    eachRow.children = children
    newTable.push(eachRow)
  })
  return (
    <Table
      style={{ width: '55vw' }}
      columns={columns}
      dataSource={newTable}
      pagination={false}
      scroll={{ x: 1500, y: 'auto' }}
    />
  )
}

OuterTable.propTypes = {
  tableData: PropTypes.array,
  columns: PropTypes.array,
}

export default OuterTable
