import React, { useEffect, useState } from 'react'
import { FormItem, useFormApi } from 'rc-form-pure'
import PropTypes from 'prop-types'

import { priceCategoryItems, powerCategoryItems, FIELDS } from '../../../constants/rreSetting'
// import { TextField } from '../../ui/form/fields/TextField'
import { SelectField } from '../../ui/form/fields/SelectField'
import { getRegionsApi, getSuppliersApi } from '../../../api/gtp'

import styles from './controls.module.less'

export const RreSettings = ({ data }) => {
  const [regionItems, setRegionItems] = useState([])
  const [supplierItems, setSupplierItems] = useState([])
  const { useWatchValue } = useFormApi()

  const supplierIdInitialValue = data[FIELDS.supplierId.fieldKey]
  const priceCategoryInitialValue = data[FIELDS.priceCategory.fieldKey]
  const regionIdInitialValue = data[FIELDS.regionId.fieldKey]
  const powerCategoryInitialValue = data[FIELDS.powerCategory.fieldKey]
  const regionIdValue = useWatchValue(FIELDS.regionId.fieldKey, regionIdInitialValue)

  useEffect(() => {
    getRegionsApi().then(response => {
      setRegionItems(
        response.data.Regions.map(item => {
          return {
            value: item.Id,
            label: item.Name,
          }
        })
      )
    })
  }, [])

  useEffect(() => {
    if (regionIdValue) {
      getSuppliersApi({ regionId: regionIdValue }).then(response => {
        setSupplierItems(response.data.Suppliers.map(item => ({ value: item.Id, label: item.Name })))
      })
    }
  }, [regionIdValue])

  return (
    <>
      <span className={styles.title}>Настройки РРЭ</span>

      <FormItem
        name={FIELDS.regionId.fieldKey}
        label={FIELDS.regionId.label}
        labelSize="small"
        initialValue={regionIdInitialValue}
        options={regionItems}
        component={SelectField}
      />

      <FormItem
        name={FIELDS.supplierId.fieldKey}
        label={FIELDS.supplierId.label}
        labelSize="small"
        initialValue={supplierIdInitialValue}
        options={supplierItems}
        component={SelectField}
      />

      <FormItem
        name={FIELDS.powerCategory.fieldKey}
        label={FIELDS.powerCategory.label}
        labelSize="small"
        initialValue={powerCategoryInitialValue}
        options={powerCategoryItems}
        component={SelectField}
      />

      <FormItem
        name={FIELDS.priceCategory.fieldKey}
        label={FIELDS.priceCategory.label}
        labelSize="small"
        initialValue={priceCategoryInitialValue}
        options={priceCategoryItems}
        component={SelectField}
      />
    </>
  )
}
RreSettings.propTypes = {
  data: PropTypes.shape({
    [FIELDS.supplierId.fieldKey]: PropTypes.number,
    [FIELDS.priceCategory.fieldKey]: PropTypes.oneOf([
      priceCategoryItems[0].value,
      priceCategoryItems[1].value,
      priceCategoryItems[2].value,
      priceCategoryItems[3].value,
    ]),
  }),
}
RreSettings.defaultProps = {
  data: {},
}
