import React from 'react'
import { useDispatch } from 'react-redux'
import { Modal, notification } from 'antd'
import { PropTypes } from 'prop-types'
import { FormBuilder } from 'rc-form-pure'
import { unwrapResult } from '@reduxjs/toolkit'

import { saveGtpAction, saveNewObjectAction } from '../../store/slices/gtpSlice'
import { Button } from '../ui/Button'
import { formatKeys } from '../../helpers/forms'
// import { FIELDS as NAME_FIELDS } from '../../constants/name'
// import { FIELDS as RRE_SETTINGS_FIELDS } from '../../constants/rreSetting'
import { FIELDS as VOLTAGE_FIELDS } from '../../constants/voltage'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'

export const AddObject = ({ isVisable, onModelClick, currentObject }) => {
  const dispatch = useDispatch()
  const handleClick = () => {
    onModelClick()
    Modal.destroyAll()
  }
  const openNotification = message => {
    notification.open({
      message,
    })
    onModelClick()
  }
  const handleSubmit = (values, errors) => {
    if (Object.keys(currentObject).length) {
      dispatch(saveGtpAction({ ...currentObject, ...values }))
        .then(unwrapResult)
        .then(() => {
          openNotification('Object updated successfuly ')
        })
    } else if (!errors) {
      const formattedFormKeys = formatKeys(values)
      const valuesToSubmit = {}

      Object.entries(formattedFormKeys).forEach(([key, value]) => {
        valuesToSubmit[key] = value
      })

      valuesToSubmit.ProfileList = []

      valuesToSubmit[VOLTAGE_FIELDS.transferPriceTable.fieldKey] = {}
      Object.entries(formattedFormKeys[VOLTAGE_FIELDS.transferPriceTable.fieldKey]).forEach(([_, value]) => {
        valuesToSubmit[VOLTAGE_FIELDS.transferPriceTable.fieldKey][value.voltage] = value.tariff
      })

      console.log('handleSubmit', { values, valuesToSubmit, formattedFormKeys })

      dispatch(saveNewObjectAction(valuesToSubmit))
        .then(unwrapResult)
        .then(() => {
          openNotification('Object created successfuly ')
          onModelClick()
        })
    }
  }

  const isEditing = Boolean(Object.keys(currentObject).length)

  return (
    <Modal
      title={isEditing ? `Edit Object ${currentObject.Name}` : 'Add New Object'}
      visible={isVisable}
      onCancel={handleClick}
      footer={null}
      width={500}
    >
      {isVisable && (
        <FormBuilder withForm onSubmit={handleSubmit}>
          {isEditing ? (
            <>
              <Name name={currentObject.Name} />
              <RreSettings data={currentObject} />
              <VoltageLevels transferPriceTable={currentObject.TransferPriceTable} />
              <OremSettings data={currentObject} />
              <CalibrationSettings data={currentObject?.Preferences?.CalibrationPreference} />
              <Indicators />
            </>
          ) : (
            <>
              <Name />
              <RreSettings />
              <VoltageLevels />

              <OremSettings />
              <CalibrationSettings />
              <Indicators />
            </>
          )}
          <div>
            <Button block type="primary" htmlType="submit" size="large">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      )}
    </Modal>
  )
}

AddObject.propTypes = {
  onModelClick: PropTypes.func.isRequired,
  isVisable: PropTypes.bool,
  currentObject: PropTypes.object,
}
AddObject.defaultProps = {
  currentObject: {},
}
