import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

import { getChart } from '../../../api/chart'

export const chartSlice = createSlice({
  name: 'chart',
  initialState: {
    list: [],
    startDate: null,
    endDate: null,
    loading: false,
    lastFetch: null,
  },

  reducers: {
    chartRequested: (chart, action) => {
      chart.loading = true
    },

    chartReceived: (chart, action) => {
      chart.list = action.payload.TimeSeriesRows
      chart.loading = false
      chart.lastFetch = Date.now()
    },

    chartRequestFailed: (chart, action) => {
      chart.loading = false
    },
  },
})

export const loadChart = createAsyncThunk('LOAD_CHART_DATA', async (params, thunkApi) => {
  const response = await getChart(params)
  const { data } = response
  thunkApi.dispatch(chartSlice.actions.chartReceived(data))
})
