import React from 'react'
import PropTypes from 'prop-types'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import { format } from 'date-fns'
import { useDispatch, useSelector } from 'react-redux'
import { Space } from 'antd'

import { RangePicker } from '../../../../components/ui/RangePicker/RangePicker'
import { gtpSelector } from '../../../../selectors/gtp'
import { loadChart } from '../../../../store/slices/chartSlice'
import { DATE_API_FORMAT } from '../../../../constants/dates'

import styles from './styles.module.less'

export const Chart = ({ timeSeriesRows }) => {
  const dispatch = useDispatch()
  const selectedGtp = useSelector(gtpSelector)

  const options = {
    chart: {
      backgroundColor: '#E4E4E4',
      spacing: 30,
      style: {
        fontFamily: 'inherit',
      },
      zoomType: 'xy',
    },
    pane: {
      background: [
        {
          backgroundColor: 'green',
          borderColor: '#cccccc',
          borderWidth: 1,
        },
      ],
    },
    title: {
      text: '',
    },
    series: timeSeriesRows.map((item, index) => {
      const seriesData = []
      Object.entries(item.History).forEach(([key, value]) => {
        seriesData.push(...value.Values)
      })

      return {
        data: seriesData,
        name: item.Name,
        color: item.Color,
      }
    }),
    // series: [
    //   {
    //     data: [-40, -70, 0, 100],
    //     color: '#BE3E4B',
    //     name: 'Факт',
    //   },
    //   {
    //     data: [-20, 20, 100, 20, 30],
    //     color: '#EA973E',
    //     name: 'Рекомендация системы',
    //   },
    // ],
    xAxis: {
      visible: false,
    },
    yAxis: {
      title: {
        text: 'МВт*ч',
        rotation: 0,
        align: 'high',
        margin: 0,
        offset: 0,
        y: -36,
        x: -5,
        style: {
          fontSize: '10px',
          color: '#3C4652',
          fontWeight: '500',
        },
      },
      labels: {
        style: {
          fontSize: '10px',
          color: '#3C4652',
          fontWeight: '500',
        },
      },
      gridLineColor: '#AAAAAA',
      tickInterval: 40,
    },
    legend: {
      enabled: false,
    },
    credits: {
      enabled: false,
    },
  }
  const handleChange = values => {
    let startDate = null
    let endDate = null

    if (values) {
      startDate = format(values[0], DATE_API_FORMAT)
      endDate = format(values[1], DATE_API_FORMAT)
    }

    dispatch(loadChart({ id: selectedGtp.Id, beginPeriod: startDate, endPeriod: endDate }))
  }
  return (
    <div className={styles.container}>
      <div className={styles.alignEnd}>
        <Space direction="vertical" size={12}>
          <RangePicker onChange={handleChange} />
        </Space>
      </div>
      <HighchartsReact highcharts={Highcharts} options={options} />
    </div>
  )
}

Chart.propTypes = {
  timeSeriesRows: PropTypes.any,
}
