import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { Header } from '../../components/Header'
import { getGtpAction, getGtpListAction } from '../../store/slices/gtpSlice'
import { gtpListSelector, gtpSelector } from '../../selectors/gtp'

import { SideBar } from './SideBar'
import { Dashboard } from './Dashboard'
import styles from './Calculator.module.less'
import { ChartContainer } from './ChartContainer'

export const Calculator = () => {
  const dispatch = useDispatch()
  const list = useSelector(gtpListSelector)
  const selectedGtp = useSelector(gtpSelector)

  useEffect(() => {
    dispatch(getGtpListAction())
  }, [dispatch])

  useEffect(() => {
    // Get the first gtp by default
    const firstGtp = list?.[0]

    if (firstGtp && !selectedGtp.Id) {
      dispatch(getGtpAction({ id: firstGtp.Id }))
    }
  }, [dispatch, list, selectedGtp.Id])

  return (
    <>
      <Header />

      <div className={styles.contentContainer}>
        <SideBar />

        <div className={styles.mainContentContainer}>
          <Dashboard />
        </div>
      </div>

      <ChartContainer />
    </>
  )
}
