import PropTypes from 'prop-types'

import { VOLTAGE_OPTIONS, TARIFF_OPTIONS } from '../constants/voltage'

export const TransferPriceTableType = PropTypes.shape(
  VOLTAGE_OPTIONS.reduce((acc, voltage) => {
    acc[voltage.value] = PropTypes.oneOf(TARIFF_OPTIONS.map(tariff => tariff.value))
    return acc
  }, {})
)
