import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'
import { format } from 'date-fns'
import ruLocale from 'date-fns/locale/ru'

import { getTable } from '../../../api/wipTableData'
export const tableSlice = createSlice({
  name: 'tableSlice',
  initialState: {
    tableData: [],
    columns: [],
  },
  reducers: {
    tableDataReceived: (table, { payload }) => {
      table.tableData = payload.tableRows
      table.columns = payload.columns
    },
  },
})

export const getTableData = createAsyncThunk('GET_TABLE_DATA', async (params, thunkApi) => {
  const response = await getTable(params)
  const { data } = response
  const tableRows = data.Retail
  const months = []
  const columns = []
  let keys = []
  keys = Object.keys(tableRows[0].PriceCategoryResult)
  months.push(keys)
  const category = {
    title: `Ценовая категория`,
    dataIndex: 'PriceCategory',
    key: 'PriceCategory',
    fixed: 'left',
    width: 120,
  }
  columns.push(category)
  for (let month = 1; month < keys.length; month++) {
    const dateM = format(new Date(keys[month]), 'do "de" MMM d yyyy', {
      locale: ruLocale,
    })
    const eachMonth = {
      title: dateM,
      dataIndex: keys[month],
      key: keys[month],
      width: 120,
    }
    columns.push(eachMonth)
  }

  const summary = {
    title: `Итого`,
    dataIndex: 'Summary',
    key: 'Summary',
    fixed: 'right',
    width: 120,
  }
  columns.push(summary)
  thunkApi.dispatch(tableSlice.actions.tableDataReceived({ tableRows, columns }))
})
