import { createSlice, createAsyncThunk } from '@reduxjs/toolkit'

import { getGtpList, getGtp, createUpdateGtp } from '../../../api/gtp'

export const getGtpListAction = createAsyncThunk('GET_GTP_LIST', async params => {
  const response = await getGtpList(params)
  return response.data
})

export const getGtpAction = createAsyncThunk('GET_GTP', async params => {
  const response = await getGtp(params)
  return response.data
})

export const saveGtpAction = createAsyncThunk('SAVE_GTP', async (params, thunkApi) => {
  const response = await createUpdateGtp(params)
  const { data } = response
  thunkApi.dispatch(gtpSlice.actions.setCurrentGtpAction(data))

  return data
})
export const saveNewObjectAction = createAsyncThunk('SAVE_NEW_OBJECT', async (params, thunkApi) => {
  const response = await createUpdateGtp(params)
  const { data } = response
  thunkApi.dispatch(gtpSlice.actions.setCurrentGtpAction(data))

  return data
})

const initialState = { list: [], currentGtp: {} }
export const gtpSlice = createSlice({
  name: 'GTP',
  initialState,
  reducers: {
    setCurrentGtpAction: (state, { payload }) => {
      state.currentGtp = payload
    },
  },
  extraReducers: {
    [getGtpListAction.fulfilled]: (state, { payload }) => {
      state.list = payload
    },

    [getGtpAction.fulfilled]: (state, { payload }) => {
      state.currentGtp = payload
    },
  },
})
