import { get, post } from './axios'

export const getGtpList = params => {
  return get('calculator/all', params)
}

/**
 *
 * @param {object} params
 * @param {string} params.id
 */
export const getGtp = params => {
  return get('calculator/object', params)
}

export const createUpdateGtp = params => {
  return post('calculator/object', params)
}

export const getRegionsApi = () => {
  return get('region/all')
}

/**
 *
 * @param {object} params
 * @param {string | number} params.regionId
 */
export const getSuppliersApi = params => {
  return get('suppliers/from_region', params)
}
