import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { gtpSelector } from '../../../../selectors/gtp'
import { tableDataSelector, getTableColumns } from '../../../../selectors/table'
import { getTableData } from '../../../../store/slices/WipTableSlice'

import OuterTable from './common/table/Table'
import styles from './Wip.module.less'

export const Wip = () => {
  const dispatch = useDispatch()
  const data = useSelector(tableDataSelector)
  const selectedGtp = useSelector(gtpSelector)
  const columns = useSelector(getTableColumns)

  useEffect(() => {
    if (selectedGtp.Id) {
      dispatch(getTableData({ id: selectedGtp.Id }))
    }
    // request new data after update Gtp
  }, [dispatch, selectedGtp])

  return (
    <>
      <div className={styles.container}>
        <div className={styles.main}>
          <span>
            <span className={styles.redBox}>1</span>
            <span>Какой то пункт</span>

            <span className={styles.redBox1}>1</span>
            <span>Отчет получен</span>
          </span>
        </div>
      </div>
      <br />
      <hr className={styles.hr} />
      <br />
      <h3>РРЭ</h3>
      <div className={styles.container}>
        <div className={styles.main}>
          <div>
            <OuterTable tableData={data} columns={columns} />
          </div>
        </div>

        <div>
          <div className={styles.tableRight}>
            <p>
              <span className={styles.tableHeadRight}>Период :</span>
              <span className={styles.tableHeadRight}>3 месяца</span>
            </p>
            <p>
              <span className={styles.tableHeadRight}>Объем:</span>
              <span className={styles.tableHeadRight}>Какой то</span>
            </p>
            <p>
              <span className={styles.tableHeadRight}>MAPE:</span>
              <span className={styles.tableHeadRight}>Какой то</span>
            </p>
            <p>
              <span className={styles.tableHeadRight}>MIN MAX:</span>
              <span className={styles.tableHeadRight}>---</span>
            </p>
            <p>
              <span className={styles.tableHeadRight}>Мощность Г:</span>
              <span className={styles.tableHeadRight}>-----</span>
            </p>
            <p>
              <span className={styles.tableHeadRight}>Мощность П:</span>
              <span className={styles.tableHeadRight}>---</span>
            </p>
          </div>
        </div>
      </div>
    </>
  )
}
