import React, { useState } from 'react'
import { FormItem } from 'rc-form-pure'
import { v4 as uuidv4 } from 'uuid'

import { SelectField } from '../../ui/form/fields/SelectField'
import { VOLTAGE_OPTIONS, TARIFF_OPTIONS, FIELDS } from '../../../constants/voltage'
import { TransferPriceTableType } from '../../../prop-types/voltageProps'
import { Button } from '../../ui/Button'

import styles from './controls.module.less'

const generateEmptyItem = () => ({ uuid: uuidv4(), item: [] })
export const VoltageLevels = ({ transferPriceTable }) => {
  let transferPriceTableEntries = Object.entries(transferPriceTable).map(item => {
    return {
      uuid: uuidv4(),
      item: item,
    }
  })
  transferPriceTableEntries = transferPriceTableEntries.length ? transferPriceTableEntries : [generateEmptyItem()]
  const [voltageLevelsGroups, setVoltageLevelsGroups] = useState(transferPriceTableEntries)

  const handleRemoveSection = key => {
    setVoltageLevelsGroups(voltageLevelsGroups => {
      const nextVoltageLevelsGroup = voltageLevelsGroups.filter(({ uuid }) => uuid !== key)
      return nextVoltageLevelsGroup
    })
  }

  const handleAddSection = () => {
    setVoltageLevelsGroups(voltageLevelsGroups => [...voltageLevelsGroups, generateEmptyItem()])
  }

  return (
    <div className={styles.voltageLevelsContainer}>
      <span className={styles.title}>Уровни напряжения</span>

      {voltageLevelsGroups.map(({ uuid, item: [initialVoltageValue, initialTariffValue] }) => (
        <FormItem key={uuid} name={`${FIELDS.transferPriceTable.fieldKey}.${uuid}`}>
          <div className={styles.voltageLevelsRowControls}>
            <FormItem
              name={FIELDS.voltage.fieldKey}
              label={FIELDS.voltage.label}
              labelSize="small"
              options={VOLTAGE_OPTIONS}
              component={SelectField}
              containerClassName={styles.voltageLevelsField}
              initialValue={initialVoltageValue}
            />

            <FormItem
              name={FIELDS.tariff.fieldKey}
              label={FIELDS.tariff.label}
              labelSize="small"
              options={TARIFF_OPTIONS}
              component={SelectField}
              containerClassName={styles.voltageLevelsField}
              initialValue={initialTariffValue}
            />
            {voltageLevelsGroups.length > 1 ? (
              <Button type="text" onClick={() => handleRemoveSection(uuid)}>
                Убрать
              </Button>
            ) : null}
          </div>
        </FormItem>
      ))}

      <Button type="text" onClick={handleAddSection}>
        Добавить
      </Button>
    </div>
  )
}
VoltageLevels.propTypes = TransferPriceTableType.isRequired

VoltageLevels.defaultProps = {
  transferPriceTable: {},
}
