import axios from 'axios'
import qs from 'qs'

export const axiosInstance = axios.create({
  // baseURL: 'https://dev.enrsoft.ru/rest',
  baseURL: 'http://localhost:8000/rest',
  headers: {
    Authorization: 'Basic YWRtaW46ZW5yX01hc3Rlcl8xNQ==',
  },
})

export const get = (url, params = {}, options) => {
  try {
    const nonEmptyParams = Object.entries(params).reduce((acc, [key, value]) => {
      if (value) {
        acc[key] = value
      }

      return acc
    }, {})

    return axiosInstance.get(url + qs.stringify(nonEmptyParams, { addQueryPrefix: true }), options)
  } catch (e) {
    console.error(e)
  }
}

export const post = (url, params, options) => {
  return axiosInstance.post(url, params, options)
}
