import React from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'
import { Input as AntInput, InputNumber as AntInputNumber } from 'antd'

import { ReactComponent as CloseIcon } from '../../../components/ui/icons/close.svg'

import styles from './Input.module.less'

export const Input = ({
  type,
  error: errorMessage,
  className,
  prefix,
  suffix,
  margin = true,
  fontSize,
  underline,
  onChange,
  ...props
}) => {
  const onChangeLocal = e => {
    switch (type) {
      case 'file': {
        onChange({ value: e.target.value, files: e.target.files })
        break
      }

      default: {
        onChange(e.target.value)
      }
    }
  }

  const isFile = type === 'file'
  const suffixLocal =
    suffix ||
    (isFile && props.value?.value && (
      <CloseIcon className="clear" onClick={() => onChangeLocal({ target: { value: '', files: [] } })} />
    ))

  const generatedProps = {
    ...props,
    className: cn(styles.input, className, { [styles._underline]: underline }),
    value: isFile ? props.value?.value || '' : props.value,
    type,
  }

  const control =
    type === 'number' ? (
      <AntInputNumber {...generatedProps} onChange={value => onChange(value)} />
    ) : (
      <AntInput {...generatedProps} onChange={onChangeLocal} />
    )

  return (
    <div className={styles.container}>
      {prefix}
      <span className={styles.inputContainer}>{control}</span>
      {suffixLocal}
    </div>
  )
}

Input.propTypes = {
  value: PropTypes.any,
  error: PropTypes.string,
  type: PropTypes.string,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,
  prefix: PropTypes.node,
  suffix: PropTypes.node,
  margin: PropTypes.bool,
  fontSize: PropTypes.string,
  underline: PropTypes.bool,
}
